   /* Hero.css */

.hero {
  height: 100vh;
  width: 100%;
  background: linear-gradient(to right, #141e30, #243b55); /* unique dark gradient */
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 1rem;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0; left: 0;
  height: 100%;
  width: 100%;
  background-image: url('https://images.unsplash.com/photo-1504384308090-c894fdcc538d?auto=format&fit=crop&w=1350&q=80');
  background-size: cover;
  background-position: center;
  opacity: 0.15;
  z-index: 0;
}

.hero-content {
  z-index: 1;
  max-width: 800px;
  margin: 0 auto;
}

.hero h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #ffffff;
}

.hero .highlight {
  color: #00e5ff;
}

.hero h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  font-weight: 400;
  color: #c0c0c0;
}

.hero p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  color: #dcdcdc;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.btn {
  background: #00e5ff;
  color: #000;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  border: none;
  border-radius: 30px;
  text-decoration: none;
  transition: background 0.3s ease;
}

.btn:hover {
  background: #00bcd4;
  color: #fff;
}

.btn-outline {
  background: transparent;
  border: 2px solid #00e5ff;
  color: #00e5ff;
}

.btn-outline:hover {
  background: #00e5ff;
  color: #000;
}