.fade-in {
  animation: fadeIn 1.2s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
body {

  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

section {
  padding: 2rem;
}

